.project2 img{
    width:500px;
    height:250px;
}
.project2{
    margin:15px;
    width:500px;
    height:250px;
    transition: transform 1000ms ease-in-out;

}
.project2:hover{
    color:white;
    
    display: flex;
    align-items: center;
    justify-content: center;
    cursor:pointer;

}

.projects2-content{
    padding:10px;
}
.projects2-content>h4{
    padding-bottom:5px;
    width:350px;
    font-size: 25px;
    color:#4070f4;
}
.projects2-content>p{
    font-size:20px;
}