.Contact{
    display: flex;
    flex-direction: column;
    color:white;
    justify-content: center;
    margin:200px 0 100px;
    align-items: center;
}
.contact_container{
    border:1px solid white;
    width:600px;
    height:200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:30px;
    margin-top:20px;
}
.contact_container>p{
    font-size:20px;
    text-align:left;
    padding-bottom:30px;
}
.contact_container>p>span{
    color:#4070f4;
}
.contact__icons>a>button>span>.MuiSvgIcon-root{
    color:white;
    font-size:30px;
}
.contact__icons>a>.MuiIconButton-root{
    color:white;
}
