@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat:wght@300&family=Poppins:ital@1&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat:wght@300;600&family=Poppins:ital@1&family=Roboto&display=swap');
.skillContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}
.skillContainer_img{
    flex:0.4;
    text-align: center;
    margin-right: 100px;
}
.skillContainer_img img{
    width:600px;
    border-radius: 3px;
    object-fit: contain;
}
.skillContainer_text{
    color:white;
    flex:0.6;
    text-align:left ;
    max-width: 500px;
    margin-bottom: 20px;
}
.skillContainer_text>h2{
    font-size: 30px;
}
.skillcontainer_Skillset>h5{
    font-family:  'Montserrat', sans-serif;
    font-weight: 400;
    padding-bottom: 10px;
    padding-top: 10px;
    font-size: 15px;

}
.skillcontainer_slider>.MuiLinearProgress-colorPrimary{
    background-color: rgb(88, 88, 88);
    
}