@import url('https://fonts.googleapis.com/css2?family=Poppins:ital@1&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Poppins:ital@1&family=Roboto&display=swap');

.header{
    display: flex;
    align-items: center;
    color:white;
    background: black;
    top:0;
    position: sticky;
    z-index: 2000;
}
.header_left{
    flex:0.4;
}
.header_left>h1{
 text-align: center;
 font-family: 'Poppins', sans-serif;
 letter-spacing: 1px;
 padding:20px 0;
}
.header_left>h1>span{
    color:#4070f4;

}
.header_right{
    display: flex;
    flex:0.6;
    justify-content: space-evenly;
    align-items: center;
}
.header_right>a>h4{
    font-size:18px;
    cursor:pointer;
    transition: transform 200ms ease-in-out;
    padding:20px 0;
    letter-spacing: 1px;
}
.header_right>a>h4:hover{
    transform:scale(1.09);
    background-color:#4070f4;
    color:white;
    padding:5px 20px;
    border-radius:2px;
    letter-spacing: 1px;
    font-family: 'Poppins', sans-serif;
    font-size:18px;
    cursor: pointer;
}
.header_rightButton{
    background-color: #4070f4;
    color:white;
    letter-spacing: 1px;
    padding:5px 20px;
    border-radius: 2px;
    cursor: pointer;
}