@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat:wght@300&family=Poppins:ital@1&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat:wght@300;600&family=Poppins:ital@1&family=Roboto&display=swap');
.topcontent{
    color:white;
}
.topcontent_container{
    position: absolute;
    top:300px;
    right:100px;
}
.topcontent_container>p{
    letter-spacing: 1px;
    text-align: center;
    padding-right: 5px;
    font-size: 25px;
    margin-top: 5px;
    font-family:  'Poppins', sans-serif;
    font-weight: 600;
    color:#8faaf2;
}

.topcontent_container>a>button{
    margin-bottom: 30px;
    text-align: left;
    margin-left: 5px;
    border:none;
    border-radius: 2px;
    outline: none;
    margin-top: 10px;
}
.topcontent_container>h1{
    font-size: 35px;
    font-family: 'Poppins', sans-serif;
    color:#f1f2f3;
}
.topcontent_downloadbutton{
    color:white;
    background-color: #4070f4;
    border-radius: 2px;
    letter-spacing: 1px;
    padding:10px 20px;
    font-size:14px;
    font-weight: 400;
    cursor: pointer;
    font-family:  'Poppins', sans-serif;
    margin-right: 20px;
}
.topcontent_downloadbutton:hover{
    color:#4070f4;
    background-color: white;
    font-weight: 400;
}
.topcontent_knowmore{
    color:white;
    border:1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding:9px 45px;
    font-family:  'Poppins', sans-serif;
    font-weight: 400;
    cursor: pointer;
}
.topcontent_knowmore:hover{
   color:black;
   background-color: white;
   font-weight: 400;
}